input.addEventListener('keyup', activeButton)

activeButton()

function activeButton () {
  if (validateEmail(input.value)) {
    valid = true
    subButton.style.background = '#fff'
  } else {
    valid = false
    subButton.style.background = 'rgba(255,255,255,0.4)'
  }
}

function validateEmail (email) {
  const re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  return re.test(String(email).toLowerCase())
}

function emailSent () {
  form.innerHTML =
    "<p class='email-sent'> Your subscription was successful </p>"
}

function getCompanies () {
  fetch(
    'https://cdn.contentful.com/spaces/bv76lgjagl2f/environments/master/entries?access_token=XAJ1wOYnMT2Aow6tufgnBbNDaU98fL0aJoB8ygy2ebA&content_type=company&include=10&order=sys.createdAt'
  )
    .then(response => response.json())
    .then(data => {
      const section = document.querySelector('.logos')

      data.items.map((item, index) => {
        const imgId = item.fields.logo.sys.id

        fetch(
          `https://cdn.contentful.com/spaces/bv76lgjagl2f/environments/master/assets/${imgId}?access_token=XAJ1wOYnMT2Aow6tufgnBbNDaU98fL0aJoB8ygy2ebA`
        )
          .then(response => response.json())
          .then(data => {
            const img = data.fields.file

            const el = document.createElement('a')
            el.href = item.fields.link
            el.target = '_blank'
            el.setAttribute('data-date', item.fields.date.substr(2, 5))
            el.setAttribute('data-tags', item.fields.tag)

            if (item.fields.repeatFounder) {
              el.setAttribute('data-tags', 'Repeat')
            }

            el.classList.add('logo')

            const link = document.createElement('p')

            link.innerHTML = el.href
              .replace(/(^\w+:|^)\/\//, '')
              .replace('www.', '')
              .replace('/', '')

            if (item.fields.linkLabel) {
              link.innerHTML = item.fields.linkLabel
            }

            const asset = document.createElement('img')
            asset.src = img.url
            asset.alt = item.fields.id
            asset.setAttribute('loading', 'lazy')

            el.append(asset)
            el.append(link)
            section.append(el)
          })
      })
    })
    .then(() => {
      setTimeout(() => {
        document.querySelector('.faded').classList.remove('faded')
        document.getElementById('date').click()
      }, 500)
    })
}

function getRepeatCompanies () {
  fetch(
    'https://cdn.contentful.com/spaces/bv76lgjagl2f/environments/master/entries?access_token=XAJ1wOYnMT2Aow6tufgnBbNDaU98fL0aJoB8ygy2ebA&content_type=company&include=10&order=sys.createdAt'
  )
    .then(response => response.json())
    .then(data => {
      const section = document.querySelector('.logos')

      data.items.map((item, index) => {
        const imgId = item.fields.logo.sys.id

        if (item.fields.repeatFounder) {
          fetch(
            `https://cdn.contentful.com/spaces/bv76lgjagl2f/environments/master/assets/${imgId}?access_token=XAJ1wOYnMT2Aow6tufgnBbNDaU98fL0aJoB8ygy2ebA`
          )
            .then(response => response.json())
            .then(data => {
              const img = data.fields.file

              const wrap = document.createElement('div')
              const parent = document.createElement('a')
              parent.href = item.fields.link
              parent.target = '_blank'
              parent.setAttribute('data-date', item.fields.date.substr(2, 5))
              parent.setAttribute('data-tags', 'New')

              parent.classList.add('logo')

              const link = document.createElement('p')
              link.innerHTML = parent.href
                .replace(/(^\w+:|^)\/\//, '')
                .replace('www.', '')
                .replace('/', '')

              const asset = document.createElement('img')
              asset.src = img.url
              asset.alt = item.fields.id

              parent.append(asset)
              parent.append(link)
              wrap.append(parent)
              section.append(wrap)

              getPrior(item.fields.repeatFounder.sys.id, wrap)
            })
        }
      })
    })
    .then(() => {
      setTimeout(() => {
        document.querySelector('.faded').classList.remove('faded')
        document.getElementById('date').click()
      }, 500)
    })
}

function getPrior (id, parent) {
  fetch(
    `https://cdn.contentful.com/spaces/bv76lgjagl2f/environments/master/entries/${id}/?access_token=XAJ1wOYnMT2Aow6tufgnBbNDaU98fL0aJoB8ygy2ebA&content_type=repeatFounder&include=10&order=sys.createdAt`
  )
    .then(response => response.json())
    .then(item => {
      const section = document.querySelector('.logos .logos__card')
      const imgId = item.fields.logo.sys.id

      fetch(
        `https://cdn.contentful.com/spaces/bv76lgjagl2f/environments/master/assets/${imgId}?access_token=XAJ1wOYnMT2Aow6tufgnBbNDaU98fL0aJoB8ygy2ebA`
      )
        .then(response => response.json())
        .then(data => {
          const img = data.fields.file

          const el = document.createElement('a')
          el.href = item.fields.link
          el.target = '_blank'
          el.setAttribute('data-date', item.fields.date.substr(2, 5))
          el.setAttribute('data-tags', 'Repeat')

          el.classList.add('logo')
          el.classList.add('prior')

          const link = document.createElement('p')
          link.innerHTML = el.href
            .replace(/(^\w+:|^)\/\//, '')
            .replace('www.', '')
            .replace('/', '')

          const asset = document.createElement('img')
          asset.src = img.url
          asset.alt = item.fields.id

          el.append(asset)
          el.append(link)

          parent.append(el)
        })
    })
}

if (window.location.href.indexOf('repeat') > -1) {
  getRepeatCompanies()
} else {
  getCompanies()
}
